<template>
	<view class="flex flex-direction">
		<!-- 是否登录判断 -->
		<is-login currentPage="index"></is-login>
		<!-- 标题 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview" >
			<block slot="content">受理工单跟踪</block>
		</cu-custom>
		<!-- 工单主要信息 -->
		<navigator class="cf bg-cyan padding-lr padding-bottom" :url="type == 'sn' ? '/pages/repair-order/repair/repairsn?preview=true' :'/pages/repair-order/repair/repairtype?preview=true'">
			<view class="fl text-lg" style="width: 80%;color: #EEEEEE;" >
				<view v-if="type == 'sn'" class="text-cut padding-xs ">
					<text class="text-bold text-white margin-right">设备SN:</text>
					<text>EBXXXXXXXXXXXXXXX</text> 
				</view>
				<view v-if="type == 'sn'" class="text-cut padding-xs "> 
					<text class="text-bold text-white margin-right">名称:</text>
					<text>OKI平推针式打印机</text>
				</view>
				<view class="text-cut padding-xs "> 
					<text class="text-bold text-white margin-right">型号:</text>
					<text>电子设备>打印机>斑马ZT210</text>
				 </view>
				 <view class="text-cut padding-xs ">
				 	<text class="text-bold text-white margin-right">提交人:</text>
				 	<text>陈*心</text>
				  </view>
				<view class="text-cut padding-xs ">
					<text class="text-bold text-white margin-right">提交时间:</text>
					<uni-dateformat :date="Date()"></uni-dateformat>
				</view>
			</view>
			<view class="fr flex  justify-center " style="width: 20%;">
				<!-- 没有真居中，样式可能存在问题 -->
				<view class="text-xxl " :class="type == 'sn' ? 'padding-tb-xl margin-top-xl' : 'padding-tb-xl'">
					<text class="cuIcon cuIcon-right text-center "></text>
				</view>
			</view>
		</navigator>
		<!-- 步骤条 -->
		<view class="bg-white padding">
			<luyj-steps :options="step != -1 ? steps0 : steps1 "  active="2" activeColor="#00BEB7"></luyj-steps>
		</view>
		<!-- 报障详情-步骤条 -->
		<view class="cu-bar bg-white margin-top-sm solid-bottom">
			<view class="action text-bold">
				报障详情
			</view>
			<view class="action">
				<button class="bg-cyan cu-btn padding-lr-xl" open-type="contact" >协作沟通</button>
			</view>
		</view>
		<view class="bg-white padding">
			<luyj-steps :options="column_steps" direction="column" :columnLine="false" active="4" activeColor="#00BEB7"></luyj-steps>
		</view>
		<view class="cf padding bg-white">
			<!-- 提交验收 -->
			<navigator class="fr margin-left" url="" @tap="finishOrder" >
				<button class="cu-btn bg-brown">提交验收</button>
			</navigator>
			<!-- 服务 -->
			<navigator class="fr margin-left" url="" @tap="serviceOrder">
				<button class="cu-btn bg-orange padding-lr">服务</button>
			</navigator>
			<!-- 转交 -->
			<navigator class="fr margin-left" url="" @tap="transferOrder">
				<button class="cu-btn bg-orange padding-lr">转交工单</button>
			</navigator>
			<!-- 受理 -->
			<view class="fr margin-left" @tap="disposeOrder">
				<button class="cu-btn bg-cyan padding-lr">受理</button>
			</view>
		</view>
	</view>
</template>

<script>
	/**
	 * (受理工单)工单跟踪
	 * @author 陆彦捷
	 * @date 2021.07.09 工单跟踪初始化页面完成
	 */
	var _self;
	export default {
		data() {
			return {
				// 样式有关参数
				preview : false,			// 是否返回上一页
				type: '',					// 类型'sn' sn报障工单 、'type' type报障工单
				step : 0 ,					// 步骤 -1 已取消 0 提交 	1.已确认 2.处理中 3.待验收 4. 已完成 
				// 系统有关参数
				steps0:[
					{title:'提交' },
					{title:'确认工单'},
					{title:'处理中' },
					{title:'已完成' },
				],					// 步骤条1 完成的步骤太欧
				steps1:[
					{title:'提交' },
					{title:'取消' },
				],					// 取消的步骤条
				// 纵向的排列（暂时不考虑分别）
				column_steps:[
					{title:'报障工单提交,等待处理' ,desc:'2021-04-01 12:00:00'},
					{title:'工单已派件' ,desc:'维修人 李先生 1585898133\n 维修方式：现场服务\n 2021-04-01 12:00:00'},
					{title:'处理中' ,desc:'2021-04-01 12:00:00'},
					{title:'其他情况' ,desc:'2021-04-01 12:00:00'},
					{title:'已验收' ,desc:'2021-04-01 12:00:00'},
					{title:'已评价' ,desc:'2021-04-01 12:00:00'},
				]
			}
		},
		onLoad:function(options){
			_self = this;
			this.getParams(options);
			// 当前时间
		},
		methods: {
			// ================================================= 初始化方法 ==========================================================
			/** 获取当前页面的参数
			 * @param {Object} options
			 */
			getParams : function(options){
				this.preview = options.preview != 'false' ? Boolean(options.preview) :'false';			// 是否返回上一页
				this.type = options.type ? options.type : 'sn';											// 报障工单或受理工单
				this.step =  !isNaN(parseInt(options.step) ) ? parseInt(options.step)  : 0;	// 步骤
			},
			// ================================================= 监听事件 ============================================================
			/** 受理
			 * @param {Object} e
			 */
			disposeOrder : function(e){
				uni.showModal({
					title:'受理',
					content:'是否受理工单？SN码'
				});
			},
			/** 转交工单
			 * @param {Object} e
			 */
			transferOrder : function(e){
				uni.showModal({
					title:'是否转交工单？',
					content:'转交工单的页面。'
				});
			},
			/** 服务工单
			 * @param {Object} e
			 */
			serviceOrder : function(e){
				uni.showModal({
					title:'服务工单',
					content:'内容：服务方式、是否收费、服务内容'					
				});
			},
			/** 完成工单
			 * @param {Object} e
			 */
			finishOrder : function(e){
				uni.showModal({
					title:'提交验收',
					content:'是否提交验收？',
					success:function(result){
						if(result.confirm){
							uni.showToast({
								title:'提交成功！'
							},2000);
							setTimeout(function(){
								uni.navigateTo({
									url:'/pages/index/index?PageCur=repair_order'
								});
							},2000);
						}
					}
				})
			},
			// ================================================= 请求方法 ============================================================
			// ======================================================================================================================
		}
	}
</script>

<style>

</style>
